@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";

  font-display: swap;
  src: url("../fonts/.woff2") format("woff2");
}

@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Montserrat";
  font-display: swap;
  src:
    url("../fonts/montserrat-extra-bold.woff2") format("woff2"),
    url("../fonts/montserrat-extra-bold.woff") format("woff");
}

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Nunito";
  font-display: swap;
  src:
    url("../fonts/nunito-bold.woff2") format("woff2"),
    url("../fonts/nunito-bold.woff") format("woff");
}

@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Nunito";
  font-display: swap;
  src:
    url("../fonts/nunito-semi-bold.woff2") format("woff2"),
    url("../fonts/nunito-semi-bold.woff") format("woff");
}

@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Nunito";
  font-display: swap;
  src:
    url("../fonts/nunito-light.woff2") format("woff2"),
    url("../fonts/nunito-light.woff") format("woff");
}

@font-face {
  font-style: italic;
  font-weight: 300;
  font-family: "Nunito";
  font-display: swap;
  src:
    url("../fonts/nunito-light-italic.woff2") format("woff2"),
    url("../fonts/nunito-light-italic.woff") format("woff");
}
